export const CustomFontName = "WhiteBoardFont";
const ValidTypes = ['embedded-opentype', 'woff', 'truetype', 'svg', 'opentype', 'woff2'];
class TextConfig {
    constructor() {
        this.fontFamily = '';
        this.fontFaceSet = false;
    }
    getFontFamily() {
        if (this.fontFamily && this.fontFaceSet) {
            return `"${this.fontFamily}", "${CustomFontName}"`;
        }
        else if (this.fontFamily) {
            return this.fontFamily;
        }
        else if (this.fontFaceSet) {
            return CustomFontName;
        }
        else {
            return 'Helvetica';
        }
    }
    setFontFamily(fontFamily) {
        this.fontFamily = fontFamily;
    }
    setFontFace(fontFace) {
        this.fontFaceSet = true;
        const fontFaceStr = fontFace.map(opt => {
            if (opt.format && !ValidTypes.includes(opt.format)) {
                console.error(`format: ${ValidTypes.join(',')}, format`);
                return `url("${opt.url}")`;
            }
            else if (opt.format) {
                return `url("${opt.url}") format("${opt.format}")`;
            }
            else {
                return `url("${opt.url}")`;
            }
        }).join(',\n');
        const newStyle = document.createElement('style');
        newStyle.setAttribute('type', 'text/css');
        newStyle.appendChild(document.createTextNode(`
@font-face {
    font-family: "${CustomFontName}";
    src: ${fontFaceStr};
}
`));
        document.head.appendChild(newStyle);
    }
}
const textConfig = new TextConfig();
export default textConfig;
